.round-button {
    box-sizing: border-box;
    width: 80px;
    height: 80px;
    border: 6px solid #fff;
    border-radius: 50%;
    color: #f5f5f5;
    text-align: center;
    text-decoration: none;
    background-color: rgba(0, 0, 0, 0.8);
    transition: all 0.3s ease;
}

.round-button:hover {
    background-color: rgba(202, 0, 0, 0.8);
    box-shadow: 0px 0px 10px rgb(133, 196, 255);
    text-shadow: 0px 0px 10px rgba(133, 196, 255, 1);
}